import React from 'react';
import { HashRouter, Routes, Route } from 'react-router-dom';

const Header = React.lazy(() => import('./header'));
const Defillama = React.lazy(() => import('./defillama'));
const Coinmarketcap = React.lazy(() => import('./coinmarketcap'));

function App() {
  return (
    <>
      <HashRouter>
        <React.Suspense fallback={null}>
          <Header />
        </React.Suspense>
        <React.Suspense fallback={null}>
          <Routes>
            <Route path="/defillama" element={<Defillama />} />
            <Route path="/coinmarketcap" element={<Coinmarketcap />} />
            <Route index element={<Defillama />} />
          </Routes>
        </React.Suspense>
      </HashRouter>
    </>
  );
}

export default App;
